//Classes Again;
class Character {
  constructor(x, y) {
    this.x = x;
    this.y = y;
  }
}
class Monster extends Character {
  constructor(x, y, name) {
    super(x, y);
    this.name = name;
    this.health_ = 100;
  }

  attack(character) {
    super.attack(character);
    // Can also be written as:
    // super(character);
  }

  get isAlive() { return this.health_ > 0; }
  get health() { return this.health_; }
  set health(value) {
    if (value < 0) throw new Error('Health must be non-negative.');
    this.health_ = value;
  }
}

var myMonster = new Monster(5,1, 'arrrg');

console.log("Monster", myMonster.health)

myMonster.health = 0;

'use strict';
/*jshint esnext: true */

// This module defines several useful generators,
// not all of which are used in the Todo app.

// A generator for iterating over the key/value pairs in an object.
export function* entries(obj) {
  for (let key of Object.keys(obj)) {
    yield [key, obj[key]];
  }
}

// A generator for iterating over the keys in an object.
export function* keys(obj) {
  for (let key of Object.keys(obj)) {
    yield key;
  }
}

// A generator that yields the first n values of an iterator.
function* take(iterator, n) {
  while (n > 0) {
    yield iterator.next();
    n--;
  }
}

// A generator for iterating over the values in an object.
export function* values(obj) {
  for (let key of Object.keys(obj)) {
    yield obj[key];
  }
}

